@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#5551ff;min-height:100%;height:100%;width:auto;padding:0;margin:0;font-family:"Inter", sans-serif;font-style:italic}a{text-decoration:none}button{outline:none}button:hover{background-color:#f8cec9}.home_container{height:100vh;width:100vw;min-width:300px;margin:0;display:flex;align-items:center;justify-content:center}.home_container Button{position:fixed;z-index:99;right:500;bottom:0;font-family:"Inter", sans-serif;font-style:italic;font-weight:500;padding:10px;background:#ffecea;border:3px solid #000;border-radius:5px}.home_container .cursor_img{position:relative;width:81px;height:129px;margin:0 0 -170px -40px}.home_box,.filmpage_button{height:150px;width:273px;min-width:175px;min-height:100px;display:flex;justify-content:center;align-items:center;padding-bottom:10px;position:relative;background:#ffecea;box-shadow:15px 15px 4px rgba(0,0,0,0.25);border:7px solid #000;border-radius:15px}.home_box p,.filmpage_button p{font-family:"Inter", sans-serif;font-size:30px;font-style:italic;font-weight:700;line-height:36px;letter-spacing:0em;text-align:center}.filmpage_container{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:20px}.film_container{width:80%;display:flex;flex-direction:column;align-items:center;justify-content:center;background:#ffecea;box-shadow:15px 15px 4px rgba(0,0,0,0.25);border:8px solid #000;border-radius:15px;padding:15px}.film_container .heading{font-size:5vh;text-align:center}.film_container .toggle_button_filmPage{right:500;bottom:0;font-family:"Inter", sans-serif;font-style:italic;font-weight:500;padding:10px;background:#ffecea;border:3px solid #000;border-radius:5px}.film_info{display:flex;flex:0.5 1;padding:20px;align-items:center;justify-content:center}.film_info .title{font-size:3vh;font-weight:700;text-align:center;text-decoration:underline}.film_info .genre{line-height:1vh}.film_info .genre,.film_info .director{font-size:2vh}.film_info p{font-size:2vh}.film_left{display:flex;align-items:center;justify-content:center;flex:0.5 1;width:50%;padding:20px}.film_left img{width:80%;border-radius:5px;margin:15px}.film_right{flex:0.5 1;align-items:center;justify-content:center;padding:20px}.film_right>*{padding:10px}.filmpage_buttons_container{position:relative;bottom:50px;display:flex}.filmpage_button{margin-right:15px}footer{position:absolute;width:100%}.logo{position:fixed;left:0;bottom:0}.tofu{position:fixed;right:0;bottom:0}@media screen and (max-width: 1000px){.filmpage_container{margin-bottom:20px}.film_container{width:85%;border:6px solid #000}.film_info{display:flex;flex-direction:column}.film_left{width:90%}.film_left img{width:85%}.film_right{margin-bottom:40px;padding:0}.film_right>*{padding:10px}.filmpage_button{width:150px;border:6px solid #000;margin:0 5px 0 5px}.filmpage_button p{font-size:1.5rem}}

